import React from "react";
import {api} from "../utils/Api";
import Card from "./Card";


class Main extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            userName: '',
            userDescription: '',
            userAvatar: '',
            cards: [],
        }
    }

    componentDidMount() {
        Promise.all([api.getUserInfo(),api.getInitialCards()])
            .then(([userData, cards]) => {
                this.setState({
                    userName: userData.name,
                    userDescription: userData.about,
                    userAvatar: userData.avatar,
                    cards: cards,
                })
            }).catch((err) => {
                console.log(err)
        })
    }

    render() {
        const {cardClick, onAddPlace, onEditProfile, onEditAvatar} = this.props;
        const {userName, userAvatar, cards, userOccupation} = this.state;
        return (

            <main className="content">
                <section className="profile">
                    <div className="profile__wrap">
                        <div className="profile__avatar"  onClick={onEditAvatar}>
                            <img className="profile__avatar-image" alt="Аватар" src={userAvatar}/>
                        </div>
                        <div className="profile__info">
                            <div className="profile__text-container">
                                <h2 className="profile__title">{userName}</h2>
                                <button className="profile__edit-button" type="button" onClick={onEditProfile}/>
                            </div>
                            <p className="profile__subtitle">{userOccupation}</p>
                        </div>
                    </div>
                    <button className="profile__add-button" type="button" onClick={onAddPlace}/>
                </section>

                <ul className="cards">
                    {cards.map((card) =>
                        <Card card={card} key={card._id} onCardClick={cardClick} />) }
                </ul>

            </main>
        )
    }

}

export default Main